*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  font-family: 'montseratt',sans-serif;
}
.app{
  background-image: url('./assets/2.png');
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease-out;
}
.app .warm{
  background-image: url('./assets/1.png');
}
main{
  min-height: 100vh;
  background-image: linear-gradient(to bottom ,rgba(0,0,0,0.2),rgba(0,0,0,.75));
  padding: 25px;
}
.search-box{
  width:100%;
  margin:0px 0 75px;
}
.search-box .search-bar{
  display: block;
  width: 100%;
  padding: 15px;
  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255,255,255,0.5);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;
  box-shadow: 0px 5px rgba(0,0,0,0.2);
  color:#313131;
  font-size: 20px;
  transition: 0.4s ease;
}
.search-box .search-bar:focus{
 background-color: rgba(255,255,255,0.75);
}
.location-box .location{
  color: #FFF;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50,50,70,0.5);
}
.location-alert{
  color: blue;
  font-size: 32px;
  font-weight: 500;
  margin: 30px;
  padding: 15px;
}
.location-box .date{
  color: #FFF;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50,50,70,0.5);
}
.weather-box{
  text-align: center;
}
.weather-box .temp{
  position: relative;
  display: inline-block;
  margin: 30px ;
  background-color: rgba(255,255,255,0.2);
  border-radius: 50px 25px;
  padding: 15px 25px;
  color: #fff;
  font-size: 102px;
  font-weight: 900;
  text-shadow: 3px 6px rgba(50,50,70,0.5);
  text-align: center;
  box-shadow: 3px 6px grba(0,0,0,0.2);

}
.weather-box .temp_child{
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  width: 40%;
  border-radius: 5px;
  position: relative;
  display: inline-block;
  margin: 30px ;
  background-color: rgba(255,255,255,0.2);
  padding: 10px;
  color: #fff;
  font-size: 30px;
  font-weight: 900;
  text-shadow: 3px 6px rgba(50,50,70,0.5);
  text-align: center;
  box-shadow: 10px 6px rgba(0,0,0,0.2);
}

.temp_child:hover{
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}
img {
  border-radius: 5px 5px 0 0;
}
.container {
  padding: 2px 16px;
  background-color: khaki;
}
.weather-box .temp_children{
  position: relative;
  display: inline-block;
  margin: 30px ;
  background-color: rgba(255,255,255,0.2);
  border-radius: 50px 25px;
  padding: 15px 25px;
  color: #fff;
  font-size: 30px;
  font-weight: 900;
  text-shadow: 3px 6px rgba(50,50,70,0.5);
  text-align: center;
  box-shadow: 3px 6px grba(0,0,0,0.2);


}
.weather-box .weather{
  color:#fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50,50,70,0.5);
}
.blink-image {
  -moz-animation: blink normal 2s infinite ease-in-out; /* Firefox */
  -webkit-animation: blink normal 2s infinite ease-in-out; /* Webkit */
  -ms-animation: blink normal 2s infinite ease-in-out; /* IE */
  animation: blink normal 2s infinite ease-in-out; /* Opera and prob css3 final iteration */
}

@-moz-keyframes blink {
  0% {
      opacity:1;
  }
  50% {
      opacity:0;
  }
  100% {
      opacity:1;
  }
} 

@-webkit-keyframes blink {
  0% {
      opacity:1;
  }
  50% {
      opacity:0;
  }
  100% {
      opacity:1;
  }
}
/* IE */
@-ms-keyframes blink {
  0% {
      opacity:1;
  }
  50% {
      opacity:0;
  }
  100% {
      opacity:1;
  }
} 
/* Opera and prob css3 final iteration */
@keyframes blink {
  0% {
      opacity:1;
  }
  50% {
      opacity:0;
  }
  100% {
      opacity:1;
  }
} 
